.profile-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10%;
  min-height: fit-content;
}

.profile-parent {
  display: flex;
  align-items: center;
  color: black;
  margin-top: 30px;
}

.profile-details-name {
  font-size: 24px;
  font-family: "Josefin Slab", serif;
}

.profile-details-role {
  display: flex;
  flex-direction: column;
  margin: 14px 0 24px 0;
}

.profile-details-role h1 {
  font-size: 40px;
  font-family: "Josefin Slab", serif;
}

.primary-text {
  color: rgb(247, 243, 243);
}

.highlighted-text {
  color: white;
}

.profile-role-tagline {
  font-size: 19px;
  margin: 5px 0 0 0;
  font-family: "Josefin Slab", serif;
}

.profile-options .highlighted-btn {
  margin: 0 0 0 20px;
}

.profile-options .highlighted-btn:hover {
  margin: 0 0 0 20px;
  background-color: rgb(250, 246, 246);
  color: rgb(6, 0, 0);
}

.profile-picture {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 0px var(--white);
  border-radius: 50%;
  height: 468px;
  width: 475px;
  margin: 0 0px 0 100px;
}

.profile-picture-background {
  height: 92%;
  width: 92%;
  background-image: url("../../../assets/Home/profilephoto.jpg");
  background-size: cover;
  border-radius: 50%;
  background-repeat: no-repeat;
}

.profile-picture-background:hover {
  transform: scale(1.07);
  transition: 1s ease-out;
}

.colz .colz-icon i {
  color: white;
  padding: 8px;
  padding-left: 0px;
}

.colz .colz-icon i:hover {
  transform: scale(1.5);
}
a {
  text-decoration: none;
  color: aliceblue;
  cursor: pointer;
}

a:hover {
  color: aliceblue;
  text-decoration: none;
  transform: scale(1.5);
  transition: 1s ease-out;
}

@media screen and (max-width: 110px) {
  .profile-picture {
    margin: 0 0px 0 80px;
  }

  .profile-details-name {
    font-size: 20px;
  }

  .profile-details-role .primary-text {
    font-size: 12px;
  }

  .profile-options .btn {
    width: 120px;
    padding: 11px 0;
    z-index: 700;
  }
}

@media screen and (max-width: 910px) {
  .profile-picture {
    height: 320px;
    width: 320px;
    margin: 0 0 0 4px;
  }
}

@media screen and (max-width: 810px) {
  .profile-picture {
    margin: 0 !important;
  }

  .profile-parent {
    flex-direction: column-reverse;
    margin: 40px 0 0 0;
  }

  .profile-options {
    display: flex;
    justify-content: space-between;
  }

  .profile-details {
    margin: 25px 0 0;
  }

  .profile-options .highlighted-btn {
    margin: 0 !important;
  }

  .profile-options,
  .profile-details-role {
    text-align: center;
  }
}

@media screen and (max-width: 375px) {
  .profile-picture {
    height: 275px;
    width: 275px;
    margin-top: -90px;
  }

  .profile-details {
    width: 90%;
  }
}
